.homepage {
  .homepage-container {
    @extend %container;
  }

  .grid {
    @include grid(4.5em);
    place-items: center;
    place-content: center;
  }
}

@media (min-width: 37.5em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 57.5em) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 76.5em) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
