.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  box-shadow: 0 0 1rem 0.2rem hsl(0, 0, 0, 0.06);

  .header-container {
    @extend %container;
    padding: 1.5rem 0;
  }

  .header-container.flex {
    @include flex;
    justify-content: space-between;
    align-items: center;
  }

  .theme-btn {
    @include btn(7.5em, true);
  }
}

.logo {
  font-size: $fs-lg;
  font-weight: $fw-bold;
}

@media (max-width: 37.5em) {
  .logo {
    font-size: $fs-sm;
  }
}
