.detailpage {
  p {
    line-height: 2em;
  }

  .back-btn {
    @include btn(8.5em, true);
  }

  .container {
    @extend %container;
  }

  .detailpage-img {
    border-radius: 0.625rem;
    overflow: hidden;
  }

  .detailpage-main.grid {
    @include grid(2.75em);
    grid-template-columns: 1fr;
  }

  .detailpage-info {
    margin-top: 1em;
  }

  .detailpage-info.flex {
    @include flex;
    flex-direction: column;
    row-gap: 2em;
  }

  .detailpage-borders {
    margin-top: 4em;
  }
}

.fa-icon {
  color: $dark-blue;
}

@media (min-width: 70em) {
  .detailpage {
    .detailpage-main.grid {
      row-gap: 0;
      grid-template-columns: 560px min(8vw, 7.5rem) minmax(min-content, 598px);
      align-items: center;
    }

    .detailpage-content {
      grid-column-start: 3;
    }

    .detailpage-info {
      margin-top: 1.5em;
    }

    .detailpage-info.flex {
      flex-direction: row;
      justify-content: space-between;
    }
  }
}
