.theme-light {
  background-color: $light-bg;
  color: $light-text;

  .back-btn,
  .theme-btn {
    background-color: transparent;
  }

  .header,
  .tag,
  .card,
  input,
  select {
    background-color: $white;
  }
}

.theme-dark {
  background-color: $dark-bg;
  color: $white;

  .header,
  .back-btn,
  .tag,
  .card,
  .theme-btn,
  input,
  select {
    color: $white;
    background-color: $dark-eles;
  }

  input::placeholder {
    color: $white;
  }

  .back-btn {
    svg {
      color: $white;
    }
  }
}
