.card {
  width: 16.5rem;
  font-size: $fs-sm;
  background-color: $white;
  min-height: 24em;
  overflow: hidden;
  box-shadow: $shadow;
  border-radius: $border;
  cursor: pointer;
  transition: box-shadow ease-in 0.2s;

  h2 {
    margin-bottom: 1rem;
  }

  .card-img {
    height: 10rem;
    width: 100%;
    overflow: hidden;

    img {
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  .card-container {
    padding-top: 1.5rem;
    padding-left: 1.5rem;
  }

  .card-content.flex {
    @include flex;
    flex-direction: column;
    gap: 0.5em;
  }

  &:hover {
    box-shadow: $hover-shadow;
  }
}
