.searchbox {
  position: relative;
  z-index: 1;
  border-radius: $border;

  svg {
    width: fit-content;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 2rem;
    transform: translateY(-50%);
  }

  input {
    @include inputfield(30rem);
    padding-left: 4.5rem;
  }
}

@media (max-width: 57.5em) {
  .searchbox {
    input {
      width: 100%;
    }
  }
}
