@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap");

// fonts
$ff-main: "Nunito Sans", sans-serif;

// font sizes
$fs-lg: 1.5rem;
$fs-rg: 1rem;
$fs-sm: 0.875rem;
$fs-xs: 0.75rem;

// font wdights
$fw-bold: 800;
$fw-mid: 600;
$fw-light: 300;

// container
$max-width: 80rem;
$inner-padding: 5vw;

// shadow
$tag-shadow: 0 0 0.2rem 0.1rem hsl(0, 0, 0, 0.06);
$input-shadow: 0 0 0.75rem 0.1rem hsl(0, 0, 0, 0.06);
$shadow: 0 0.1rem 1rem 0.3rem hsl(0, 0, 0, 0.06);
$hover-shadow: 0 0 0.5rem 0.3rem hsl(0, 0, 0, 0.09);

// borders
$border: 0.375rem;

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: $ff-main;
  font-size: $fs-rg;
  font-weight: $fw-light;
}

main {
  min-height: 100vh;
  padding: 5rem;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.25rem;
}

p {
  line-height: 1rem;
}

input,
textarea,
select,
button {
  font-size: $fs-sm;
}

img,
svg,
picture {
  display: block;
  width: 100%;
}

a {
  all: unset;
  cursor: pointer;
}

%container {
  width: min($max-width, 100% - $inner-padding * 2);
  margin-inline: auto;
}

@mixin grid($row: 0, $col: 0) {
  display: grid;
  column-gap: $col;
  row-gap: $row;
}

@mixin flex {
  display: flex;
}

.toolbar {
  padding: 1.5rem 0 2.5rem;
  display: flex;
  flex-direction: column;
  row-gap: 2.5em;
}

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

.invisible {
  display: none !important;
}

@mixin inputbase($width) {
  border: 1px solid transparent;
  border-radius: $border;
  max-width: 100%;
  width: $width;
}

@mixin inputfield($width) {
  @include inputbase($width);

  height: 4em;
  box-shadow: $input-shadow;
  line-height: 1.5em;
}

@mixin btn($width, $isicon: false) {
  @include inputbase($width);
  height: 2.5em;
  cursor: pointer;
  transition: color 0.2s ease-in;

  @if $isicon {
    display: flex;
    gap: 0.75em;
    place-items: center;
    place-content: center;
    transition: color ease-in 0.2s svg ease-in 0.2s;
  }

  svg {
    @if isicon {
      width: fit-content;
    }
  }

  &:hover {
    font-weight: $fw-mid;
  }
}

@media (min-width: 57.5em) {
  .toolbar {
    padding: 3rem 0;
    row-gap: 0;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (max-width: 37.5em) {
  input,
  textarea,
  select,
  button {
    font-size: $fs-xs;
  }
}
